        p Para iniciar la revisin de los conceptos relacionados con el plan de accin, es importante resaltar otros temas que hacen parte del proceso. El plan de accin es una herramienta de tipo administrativo, por medio de la cual la organizacin o empresa traza una lnea de actividades con el fin de obtener un resultado positivo en el alcance de los objetivos empresariales propuestos. Los planes de accin estn encaminados hacia diversas reas de la empresa y se requiere de una adecuada proyeccin de objetivos (general y especficos) as como los recursos tangibles (materiales, tecnologa, fsicos, econmicos y financieros) e intangibles (recurso humano: conocimiento, experiencia, motivacin) para alcanzar el cumplimiento de las actividades propuestas.  
        p Este tipo de herramientas sirven para organizar las actividades y planificar de manera adecuada, lo que contribuye en disminuir el margen de error, optimizar los recursos e ir avanzando poco a poco en el alcance de las metas propuestas. Las caractersticas generales de un plan de accin se pueden describir como: 
        ul.lista-ul.ms-3
          li.mb-0
            span.icon  
            | Permite desglosar los objetivos de largo, corto y mediano plazo, de forma que se puedan alcanzar. 
          li.mb-0
            span.icon  
            | Permite describir las actividades a realizar para alcanzar cada objetivo propuesto. 
          li.mb-0
            span.icon  
            | El plan de accin responde a una o varias necesidades en especfico. 
          li.mb-0
            span.icon  
