              p.mb-1 Una supervisin gil y fluida requiere herramientas tipo #[em software]. Medir indicadores es clave, muestra el cumplimiento o incumplimiento de metas establecidas, en la revisin de avances, evaluar al equipo ejecutor y as avanzar en nuevas tareas o reforzar las que no se han cumplido.
      
    p.mb-5(data-aos="fade-down") En conclusin, el plan de accin corresponde con las tareas que deben ejecutar en cada rea o unidad para aportar a las estrategias del plan operativo o plan estratgico, lo cual permite monitorear, tener adecuado seguimiento y la respectiva evaluacin.

    .row.justify-content-center.align-items-center.mb-5(data-aos="zoom-in-down")
      .col-12.col-lg-10
