          .col-12.col-md-9.col-lg 
            p.mb-0 Para que el plan de accin sea operativo y efectivo se debe tener presente elementos adicionales como los niveles de jerarqua, responsabilidad, manual de funciones, actividades y tareas de cada rea. Es importante conocer que las dinmicas organizativas se estructuran segn los mtodos y procedimientos que son internos en las organizaciones. Estas estructuras son la base de la operatividad en las diversas reas de la empresa, por tanto, se hace necesario a nivel general conocer los grados de autoridad y responsabilidad acorde con las diversas jerarquas de la empresa. 

    p.mb-3(data-aos="fade-down") En la organizacin empresarial se destacan dos tipos de estructuras que son las centralizadas y las descentralizadas. Las estructuras centralizadas se caracterizan porque los cargos de nivel directivo sobresalen al resto de los miembros de la empresa; por otro lado, las estructuras descentralizadas se caracterizan porque los cargos directivos asignan las responsabilidades en niveles intermedios o bajos.
    p.mb-5(data-aos="fade-down") A nivel organizativo se destacan: la estructura organizativa funcional, la jerrquica, la estructura lineal, la estructura matricial y la estructura por divisin, como se describe brevemente a continuacin:

    TabsC.color-acento-botones.mb-5(data-aos="zoom-in-down")
      .py-3.py-md-4(titulo="Estructura organizativa funcional")
        .row
          .col-md-5.mb-4.mb-md-0
            figure
              img(src='@/assets/curso/temas/tema1/tema1-3.svg', alt='')
          .col-md-7
            h4 Estructura organizativa funcional
            p Tiene como objetivo dar soporte a los diversos niveles jerrquicos, es decir, frente a cada nivel de jerarqua se encuentra un especialista o jefe de seccin. Este tipo de sistema combate la multiplicidad de funciones y el trabajo se enfoca en los objetivos concretos. 
      .py-3.py-md-4(titulo="Estructura organizativa lineal o <em>staff</em>")
        .row
          .col-md-5.mb-4.mb-md-0
            figure
              img(src='@/assets/curso/temas/tema1/tema1-4.svg', alt='')
          .col-md-7
            h4 Estructura organizativa lineal o #[em staff]
            p Es una estructura que mezcla las relaciones de autoridad directa con aquel asesoramiento que ejercen consultores externos a la empresa. En este tipo de dinmica, los asesores o agentes consultores cubren los requerimientos de la empresa, no hay una amenaza sobre la autoridad y las actividades suelen realizarse con tiempos y recursos ms ptimos. 
      .py-3.py-md-4(titulo="Estructura organizativa matricial")
        .row
          .col-md-5.mb-4.mb-md-0
            figure
              img(src='@/assets/curso/temas/tema1/tema1-5.svg', alt='')
          .col-md-7
            h4 Estructura organizativa matricial
            p Es la unin de varios recursos y materiales para ejecutar tareas especficas y cuando se finalice la tarea o proyecto, la estructura se disuelve. Como caracterstica principal, las personas que integran los equipos pueden o no hacer parte de la empresa y tienen como jefes uno que es general y el otro que es responsable de la actividad especfica. 
            p Este tipo de estructura se divide en dos: 
            ul.lista-ul.ms-3
              li
                span.icon  
                | Distribucin vertical (cada departamento o rea est dedicada a una funcin especfica). 
              li
                span.icon  
                | Distribucin horizontal (integracin de los trabajadores de diversas reas con un proyecto especfico). 
      .py-3.py-md-4(titulo="Estructura organizativa por divisin")
        .row
          .col-md-5.mb-4.mb-md-0
            figure
              img(src='@/assets/curso/temas/tema1/tema1-6.svg', alt='')
          .col-md-7
            h4 Estructura organizativa por divisin
            p Se organiza bajo tres lneas claves que son productos, clientes o zona geogrfica. 
            ul.lista-ul.ms-3
              li
                span.icon  
                | Estructura por productos: esta estructura se divide en grupos y cada uno se hace cargo de un producto en especfico.  
              li
                span.icon  
                | Estructura por zona geogrfica: es aquella estructura que se divide por grandes estructuras acorde con las cadenas organizativas.  Los directivos estn a cargo de cada zona acorde con lo asignado. 
              li
                span.icon  
                | Estructura por clientes: depende de la necesidad de solucin de los clientes, llegando a tener procesos de atencin al usuario ms concretos y bien definidos.
      .py-3.py-md-4(titulo="Estructura organizativa jerrquica")
        .row
          .col-md-5.mb-4.mb-md-0
            figure
              img(src='@/assets/curso/temas/tema1/tema1-7.svg', alt='')
          .col-md-7
            h4 Estructura organizativa jerrquica
            p Ms utilizada a nivel empresarial. Es la creacin de pequeas dependencias vigiladas por uno o varios cargos superiores. Concentra la autoridad y poder sobre otras dependencias. Permite organizar los niveles de autoridad, responsabilidad y actividades asignadas hasta llegar al nivel de mayor autoridad.
            p La divisin vertical debe distribuir responsabilidades de decisin, para alcanzar objetivos. Tiene varios niveles:
            ul.lista-ul.ms-3
              li
                span.icon  
                | Nivel superior o estratgico: responsable de elaborar estrategias y polticas empresariales. Define objetivos a largo plazo y toma decisiones sobre necesidades y requerimientos. Se ubica al presidente, el director, el gerente. 
              li
                span.icon  
                | Nivel medio o tctico: encargado de regular acciones y actividades que se desarrollan en el nivel inferior u operativo. Se ubica el gerente de ventas, el gerente de produccin.
              li
                span.icon  
                | Nivel inferior u operativo: ejecutan previa programacin las tareas rutinarias de la organizacin. Se ubican      los obreros, los empleados administrativos, por ejemplo.

    .row.justify-content-center.align-items-center.mb-5(data-aos="fade-down")
      .col-12.col-md-8.col-lg-7
        p Adicional, a la necesidad de comprender los tipos de estructuras organizacionales, es necesario recordar que todo colaborador de la empresa u organizacin debe conocer el #[b manual de funciones], ya que este es una herramienta de gestin del talento humano, que establece las funciones y competencias laborales que hacen parte de todos los empleos de la organizacin. En este manual de funciones debe existir claridad respecto a los requerimientos de conocimientos, experiencia y las competencias que sean exigidas y necesarias para el desempeo del cargo.  
        p El manual de funciones se construye teniendo en cuenta elementos claves como la misin, objetivos y funciones, estructura, procesos (de tipo misional, estratgico, apoyo, evaluacin y seguimiento) y los cargos de la institucin.
        p Todo manual de funciones debe contener la siguiente informacin: 
        ul.lista-ul.ms-3
          li.mb-0
            span.icon  
            | Identificacin y ubicacin del empleo. 
          li.mb-0
            span.icon  
            | Contenido funcional (contiene el propsito principal y la descripcin de funciones esenciales del empleo).
          li.mb-0
            span.icon  
            | Conocimientos bsicos o esenciales, competencias de tipo comportamental y requisitos de experiencia y formacin acadmica necesarios para el desempeo de funciones.
      .col-10.col-md-4.col-lg-5
        img(src='@/assets/curso/temas/tema1/tema1-8.png', alt='')  

    p.mb-5(data-aos="fade-down") Los objetivos principales del manual de funciones se describen a continuacin:

    .row.justify-content-center.align-items-center.mb-5 
