          .tarjeta.color-acento-contenido.p-3(x="50%" y="10%" numero="+")
            p #[b ESTABLECER ACTIVIDADES:] las actividades realizadas al interior de la empresa para la obtencin de los objetivos misionales.
          .tarjeta.color-acento-contenido.p-3(x="70%" y="22%" numero="+")
            p #[b ALINEAR PUESTOS DE TRABAJO] con estructura organizacional definida (cualquiera que se haya escogido). Debe existir armona entre la estructura organizacional y los puestos de trabajo definidos, esto acta positiva o negativamente en la productividad y financieramente en la operacin.
          .tarjeta.color-acento-contenido.p-3(x="76%" y="49%" numero="+")
            p #[b OPTIMIZAR LA PRODUCTIVIDAD] de la empresa por medio del anlisis en las actividades operativas teniendo en cuenta la forma en que se desarrolla, tiempos, acciones y todos los requerimientos de formacin y capacitacin.
          .tarjeta.color-acento-contenido.p-3(x="68%" y="77%" numero="+")
            p #[b INDUCCIN DE PERSONAL,] contar con un mtodo de induccin del personal que ingresa nuevo a la empresa, y de igual manera permite valorar la productividad y desempeo del empleado en el marco del puesto de trabajo.
          .tarjeta.color-acento-contenido.p-3(x="50%" y="87%" numero="+")
            p #[b CONCRETAR EL PERFIL DEL PERSONAL] que se requiere para la ejecucin de las actividades permitiendo la bsqueda y la seleccin del personal que se requiere en la empresa. 
          .tarjeta.color-acento-contenido.p-3(x="34%" y="77%" numero="+")
            p #[b CUMPLIMIENTO DE INSTRUCCIONES] de trabajo. Agrupar, intervenir y prevenir sus modificaciones de origen arbitrario.     
          .tarjeta.color-acento-contenido.p-3(x="25%" y="49%" numero="+")
            p #[b ESTABLECER RESPONSABILIDADES] por fallas o errores, de una manera ms simple. 
          .tarjeta.color-acento-contenido.p-3(x="30%" y="23%" numero="+")
            p #[b INCREMENTAR LA EFICIENCIA] de los empleados, dndoles las indicaciones de lo que deben hacer y cmo lo deben de realizar, mejorando de esta manera la coordinacin del trabajo e impidiendo las rplicas.
      .col-12.d-block.d-md-none
