          img.d-block.d-md-none(src='@/assets/curso/temas/tema1/info-3b.svg', alt='La infografa indica que para la elaboracin del manual de funciones se debe contar con elementos como: objetivo, responsable, descripcin de las actividades, datos bsicos y seguimiento y control.')

    p.mb-3(data-aos="fade-down") Para finalizar, se debe tener en cuenta que el contenido de un manual puede variar acorde con los requerimientos de la empresa, por tanto, adicional a los elementos descritos en los prrafos anteriores, es necesario como complemento:  portada, misin y visin, ndice, hojas de autorizacin del rea, poltica de calidad (cuando sea aplicable) objetivo(s) del manual, bitcora de revisiones y modificaciones a polticas y procedimientos, formatos, artculos y anexos.
    p.mb-5(data-aos="fade-down") Para la revisin de los elementos bsicos del manual de funciones, se deben diferenciar los trminos que se definen a continuacin:

