      h2 1.1 Metodologas

    p.mb-3(data-aos="fade-down") Conceptos correspondientes a los procesos, procedimientos y estndares de calidad, son necesarios para entender las metodologas que hacen parte del plan de accin.
    p.mb-5(data-aos="fade-down") Primero se debe hacer la diferenciacin entre los conceptos de proceso y procedimiento. Acorde con lo expuesto en la ISO 9000, el proceso se define como el #[em conjunto de actividades mutuamente relacionadas que al interactuar juntas convierten los elementos de entrada en resultados]. Por otro lado, el procedimiento es la forma en que se ejecutan una serie de pasos para obtener un resultado especfico. A continuacin, se describen los elementos claves para diferenciar el proceso y el procedimiento respectivamente. 

    .tarjeta--container.row.mb-5(data-aos="zoom-in-down")
      .col-md.tarjeta.color-primario.p-5
        .row.justify-content-center.mb-4
          .col-6
            figure
              img(src='@/assets/curso/temas/tema1/tema1-13.png', alt='')
        ul.lista-ul.ms-3
          li.mb-0
            span  
            | Tienen en cuenta un objetivo general.  Suelen integrarse varios procesos lo que se denomina, Mapa de procesos. 
          li.mb-0
            span  
            | Interactan varias personas de diferentes reas de la organizacin. 
          li.mb-0
            span  
            | Est distribuido por etapas. 
          li.mb-0
            span  
            | Su prctica es continua.
          li.mb-0
            span  
            | Son dinmicos (pueden cambiar con frecuencia).
          li.mb-0
            span  
            | Parten de la necesidad y la voluntad de lograr un resultado. Se dividen en procesos estratgicos, misionales y de apoyo. 

      .col-md.tarjeta.color-secundario.p-5
        .row.justify-content-center.mb-4
