    p.mb-5(data-aos="fade-down") El manual de procesos es tambin conocido como el manual de procedimientos y es aquel que le permite a la empresa un funcionamiento correcto ya que por medio de este se organizan todos los elementos necesarios para la gestin de la organizacin (polticas, normas, reglamentos). Los aspectos positivos del manual son: la optimizacin del tiempo, el control a nivel interno, mejorar los procesos, favorecer la comunicacin, asignar responsabilidades, definir expectativas y capacitar a los colaboradores internos en el marco de las actividades que se deben realizar, una breve descripcin al respecto es la siguiente:

    .row.justify-content-center.mb-5(data-aos="fade-down")
      .col-12.col-lg-10.col-md-11.mb-2.mb-md-0
        PasosA.color-primario(tipo="n")
          .row.align-items-center(data-aos="fade-left")
            .col-md-6.mb-4.mb-md-0
              h4 Elementos bsicos
              p Los elementos bsicos que conforman el manual de procedimientos son los siguientes: 
              ul.lista-ul.ms-3
                li.mb-0
                  span.icon  
                  | Ttulo.   
                li.mb-0
                  span.icon  
                  | Marco normativo.
                li.mb-0
                  span.icon  
                  | Objetivo del documento. 
                li.mb-0
                  span.icon  
                  | Responsables y personal que realiza la revisin.
                li.mb-0
                  span.icon  
                  | Describir cada uno de los procesos. 
                li.mb-0
                  span.icon  
                  | Estructurar el diagrama de flujo. 
                li.mb-0
                  span.icon  
                  | Organizar el glosario de trminos. 
            .col-md-6
              figure
                img(src='@/assets/curso/temas/tema1/tema1-15.png', alt='')
          .row.align-items-center(data-aos="fade-right")
            .col-md-6.mb-4.mb-md-0
              figure
                img(src='@/assets/curso/temas/tema1/tema1-16.png', alt='')
            .col-md-6.text-start
              h4 Actividades
              p Por lo general, cada manual de procedimiento debe contener las siguientes actividades: 
              ul.lista-ul.ms-3
                li.mb-0
                  span.icon  
                  | Nombre del procedimiento.
                li.mb-0
                  span.icon  
                  | Objetivo.
                li.mb-0
                  span.icon  
                  | Polticas y normas de operacin.
                li.mb-0
                  span.icon  
                  | Descripcin narrativa del procedimiento.

          .row.align-items-center(data-aos="fade-left")
            .col-md-6.mb-4.mb-md-0
              h4 Responsable
              p Nombre completo del rea y/o puesto que realiza la actividad (maysculas y minsculas), y corresponder con la estructura orgnica. Si una misma rea realiza actividades continuas; va el nombre en la primera, y al inicio de cada hoja (de continuar la actividad). Se usan siglas, cuando la nomenclatura es extensa.
            .col-md-6
              figure
                img(src='@/assets/curso/temas/tema1/tema1-17.png', alt='')
          .row.align-items-center(data-aos="fade-right")
            .col-md-6.mb-4.mb-md-0
              figure
                img(src='@/assets/curso/temas/tema1/tema1-18.png', alt='')
            .col-md-6.text-start
              h4 Nmero
              p Enuncia las actividades en orden lgico acorde con la aparicin de cada una y en el marco de las alternativas de solucin. Se recomienda usar nmeros arbigos. 
          .row.align-items-center(data-aos="fade-left")
            .col-md-6.mb-4.mb-md-0
              h4 Descripcin de la actividad
              p Se detallan todas las actividades partcipes del procedimiento en una forma secuencial, clara y precisa en el marco del cmo, cundo y dnde se llevan a cabo estas. Escribir el inicio del procedimiento describiendo: Inicia procedimiento. Cada actividad empieza con un verbo indicativo en tercera persona del singular.
            .col-md-6
              figure
                img(src='@/assets/curso/temas/tema1/tema1-19.png', alt='')
          .row.align-items-center(data-aos="fade-right")
