            .col-md-6.text-start
              h4 Tiempo
              p Se debe tener presente el tiempo necesario para la ejecucin de cada actividad, cuantificando por das hbiles, si no es posible establecer el tiempo, se debe poner el tiempo total del procedimiento y su respectiva justificacin.

    p.mb-5(data-aos="fade-down") Algunas consideraciones que deben tenerse en cuenta en las actividades del manual de procedimientos son las siguientes:

    .row.justify-content-center.mb-5
      .col-12.col-lg-10.col-md-11.mb-2.mb-md-0(data-aos="fade-left")
