    p.mb-3(data-aos="fade-down") En toda descripcin se debe tener en cuenta los siguientes elementos: Quin o qu rea lo hace?, Cmo lo hace?, Cundo lo hace? y Dnde lo hace?.
    p.mb-5(data-aos="fade-down") Para realizar una descripcin grfica y secuencial de las actividades que integran los procedimientos se puede hacer mediante un diagrama de flujo, aqu, cada actividad debe ser identificada con su respectivo nmero, agregndole por fuera del smbolo al que le corresponda a la actividad, ubicndolo en el ngulo superior derecho, acorde con la numeracin de actividades la cual corresponde con la descripcin narrativa. Los smbolos bsicos del flujograma son los siguientes:

    .row.justify-content-center.mb-5
      .col-12.mb-2.mb-md-0(data-aos="fade-left")
