          .anexo__texto
            p #[b Anexo]. Figura 3. Diagrama de flujo 

    p.mb-3(data-aos="fade-down") El diagrama de flujo puede ser trabajado por columnas y se deben proyectar tantas columnas como sean necesarias para las reas responsables que participan dentro del procedimiento. Por temas de orden, se sugiere un mximo de tres columnas; si se da el caso que en un procedimiento participen ms de tres reas, estas se deben agrupar acorde con el rea jerrquica descendente del titular, esto con fin de prevenir una aglomeracin de columnas que dificultan tanto la elaboracin, como la comprensin del diagrama.
    p.mb-5(data-aos="fade-down") Para una correcta ejecucin de los planes de accin se deben conocer las diversas metodologas que se pueden desarrollar, tanto para el diseo de los planes, programas y proyectos como para la definicin de prioridades. Pero antes de comenzar con la revisin de estos temas, es importante conocer aquellos conceptos relacionados con los estndares de calidad, ya que esto ayudar en la comprensin de las metodologas en las que se va a profundizar ms adelante. 

    .row.justify-content-center.align-items-center.mb-5(data-aos="zoom-in-down")
      .col-12.col-lg-10
        .row.align-items-center.tarjeta.color-secundario-c.p-4
          .col-10.col-md-3.col-lg-4
            img(src='@/assets/curso/temas/tema1/tema1-21.png', alt='')
          .col-12.col-md-9.col-lg 
            p.mb-0 La calidad en trminos generales, se define como la propiedad que tiene una cosa u objeto, que define su valor, y la satisfaccin que genera en quien hace uso de la misma. En el marco de la salud, la calidad es definida como el grado en que los servicios de salud ofertados pueden ayudar a que el individuo y la poblacin en general puedan aumentar los resultados sanitarios planteados; y la calidad ambiental es aquella que representa las condiciones y caractersticas del ambiente en torno a la disponibilidad de acceder a los recursos naturales, a los ambientes con condiciones sanitarias ptimas y por tanto comprende la ausencia de agentes nocivos para la salud de la poblacin. 

    .row.justify-content-center.align-items-center.mb-5(data-aos="fade-down")
      .col-10.col-md-4.col-lg-5
        img(src='@/assets/curso/temas/tema1/tema1-22.png', alt='')  
      .col-12.col-md-8.col-lg-7
        p El estndar de calidad, es definido como el punto de referencia que se tiene en cuenta para determinar si un objeto, servicio o proceso, por ejemplo, cumplen con las expectativas del usuario y tambin con las que estn reguladas normativamente. Por tanto, el estndar de calidad es aquel que se debe cumplir, es decir, es parte del proceso evaluativo por parte de los entes que regulan el sector. Algunos estndares de calidad pueden ser catalogados como de obligatorio cumplimiento y otros, como aquellos que son superiores y que pueden aplicar para procesos de certificacin o acreditacin segn sea el caso. Es as como en salud ambiental y sanitaria es necesario cumplir con los estndares de calidad definidos para que la poblacin pueda contar con el hbitat saludable y con las ptimas condiciones de salud relacionadas con las condiciones ambientales.  

    p.mb-5(data-aos="fade-down") Tal y como ya fue revisado en este mismo programa, las actas de inspeccin, vigilancia y control, sirven de ejemplo para identificar los estndares de calidad esperados a nivel sanitario. A continuacin, se enuncian algunos criterios contenidos dentro del acta general de IVC sanitaria revisados en los riesgos ambientales, del consumo y sanitarios segn la salud pblica. 

    .row.justify-content-center.align-items-center.mb-5
      .col-12.col-lg-10(data-aos="fade-right")
        .titulo-sexto.color-acento-contenido.mb-1
          p.titulo.pb-0.mb-0 #[b Tabla 1.] #[i Ejemplo estndares de calidad IVC Sanitaria] 
        .tabla-a.color-acento-contenido
          table(style="min-width:768px")
            caption Nota: adaptado de Histrico.santander.gov.co.
            thead
              tr.cabecera-a.text-center
                th(style="width:15%") No 
                th 3.1 Abastecimiento de agua
            tbody
              tr
                td 3.1.1
                td.text-start El establecimiento est conectado a la red del sistema de acueducto o el sistema que lo supla.
              tr
                td 3.1.2
                td.text-start El establecimiento cuenta con el suministro y tiene acceso a agua potable.
              tr
                td 3.1.3
                td.text-start Las instalaciones de suministro en el interior, estn organizadas y construidas para su normal funcionamiento con la dotacin requerida para el servicio continuo y con presin suficiente en todos los lugares de consumo.
              tr
                td 3.1.4
                td.text-start El establecimiento tiene tanques para el almacenamiento del agua potable, los cuales se encuentran protegidos y con la capacidad requerida para garantizar el abastecimiento en todas las instalaciones. 
              tr
                td 3.1.5
                td.text-start El establecimiento realiza de manera semestral o cuando sea requerido, la limpieza y desinfeccin de los tanques de almacenamiento de agua potable.

    .row.justify-content-center.align-items-center.mb-5(data-aos="fade-down")
      .col-12.col-md-8.col-lg-8
        p Adicional a lo anterior, es importante tener en cuenta que los estndares de calidad pueden generarse a partir de los lineamientos gubernamentales, pero tambin pueden ser parte del sistema de gestin de calidad de la organizacin, lo cual complementa las acciones en calidad dirigidas a los procesos de mejora continua fundamentales dentro del proceso de atencin en salud, rea de salud ambiental. 
        p Los estndares de calidad, tienen como objetivo central responder positivamente ante la auditora externa de las organizaciones. En el marco de la evaluacin de los modelos de estndares, se establecen los requerimientos de los cuales sin su cumplimiento no se puede contar con la certificacin correspondiente. Tener una certificacin externa genera una garanta hacia terceros del correcto funcionamiento del sistema de calidad de la organizacin o un rea especfica. 
        p Los estndares de calidad no gubernamentales ms utilizados por las entidades son las normas ISO - #[em Norma definida por la Organizacin Internacional de Normalizacin que se aplica a los productos y servicios]. Se pueden encontrar las siguientes: 
      .col-10.col-md-4.col-lg-4
        img(src='@/assets/curso/temas/tema1/tema1-23.png', alt='')  

    .row.justify-content-center.mb-5(data-aos="fade-down")
      .col-lg-6.d-flex.align-items-stretch
        .tarjeta-avatar-b.mb-3
          .tarjeta-avatar-b__img
            img(src='@/assets/curso/temas/tema1/tema1-24.png' alt='')
          .tarjeta.color-primario
            .p-4
              h4 ISO 9000:2015 
              p Corresponde con el Sistema de gestin de la calidad, especficamente las bases y el vocabulario. 
      .col-lg-6.d-flex.align-items-stretch
        .tarjeta-avatar-b.mb-3
          .tarjeta-avatar-b__img
