          .tarjeta.color-primario
            .p-4
              h4 ISO 9001:2015 
              p Corresponde con el Sistema de gestin de la calidad, especficamente los requisitos. 
      .col-lg-6.d-flex.align-items-stretch
        .tarjeta-avatar-b.mb-3
          .tarjeta-avatar-b__img
            img(src='@/assets/curso/temas/tema1/tema1-26.png' alt='')
          .tarjeta.color-primario
            .p-4
              h4 ISO 9002:2016 
              p Corresponde con el Sistema gestin de la calidad, especficamente las directrices para la aplicacin de la Norma ISO 9001:2015. 
      .col-lg-6.d-flex.align-items-stretch
        .tarjeta-avatar-b.mb-3
          .tarjeta-avatar-b__img
            img(src='@/assets/curso/temas/tema1/tema1-27.png' alt='')
          .tarjeta.color-primario
            .p-4
              h4 ISO 9004:2018 
              p Corresponde con el Sistema de Gestin de la calidad, especficamente la calidad de la organizacin y los lineamientos para lograr el xito sostenido.

    p.mb-3(data-aos="fade-down") Para concluir, es necesario tener en cuenta el contraste entre cada lnea de estndares de calidad que se pueden encontrar en el marco de las actividades relacionadas con la salud ambiental y sanitaria. Recordar que anteriormente se revisaron los lineamientos del Sistema Obligatorio de Garanta de la Calidad, y los de inspeccin, vigilancia y control sanitaria en el marco del componente de salud ambiental. 
    p.mb-5(data-aos="fade-down") En el siguiente resumen, se describen los estndares de calidad en relacin a los lineamientos de inspeccin, vigilancia y control sanitarios, y del Sistema Obligatorio de Garanta de la Calidad SOGC-.

    .row.justify-content-center.align-items-center.mb-5
      .col-12.col-lg-12(data-aos="fade-right")
        .titulo-sexto.color-acento-contenido.mb-1
          p.titulo.pb-0.mb-0 #[b Tabla 2.] #[i Tabla resumen presentacin estndares de calidad] 
        .tabla-a.color-acento-contenido
          table(style="min-width:768px")
            thead
              tr.cabecera-a.text-center
                td(style="width:25%")
                td
            tbody
              tr
                th(rowspan="3") Sistema Obligatorio Garanta de la Calidad SOGC
                td.text-start 
                  p SUH: Sistema nico de habilitacin. El proceso de habilitacin est regulado por la resolucin 3100 de 2019. Contiene estndares de calidad enfocados en la capacidad tcnico administrativa, la suficiencia patrimonial y financiera; y la capacidad tecnolgica y cientfica.
                  p Ejemplo 1: Numeral 11.1.2, Estndar de infraestructura, Generalidades de las condiciones de orden, aseo, limpieza y desinfeccin, estndar de calidad nmero 41: Las condiciones de orden, aseo, limpieza y desinfeccin son evidentes y responden a un proceso dinmico de acuerdo con los servicios prestados.
              tr
                td.text-start
                  p SUA: Sistema nico de Acreditacin. Manual de Acreditacin en Salud Ambulatorio y Hospitalario de Colombia, versin 3.1. 
                  p Ejemplo 1: Grupo de estndares asistenciales, Salida y seguimiento.  Estndar de calidad 52. Cdigo: (AsSAL2) La organizacin asegura un plan de coordinacin con otras organizaciones y comunidades relevantes en la prevencin de enfermedades y la promocin, proteccin y mejoramiento de la salud de la poblacin a la que presta sus servicios. 
                  p #[b Criterios:] 
                  ul.lista-ul.ms-3
                    li.mb-0
                      span.icon  
                      | La organizacin asegura que las polticas, directrices, procesos y procedimientos para la prevencin de enfermedades y promocin de la salud estn alineados con las normas nacionales y territoriales de salud pblica. 
                    li.mb-0
                      span.icon  
                      | La organizacin asegura la existencia y aplicacin de directrices y/ o procedimientos para el seguimiento de la prevencin de las enfermedades y la salud despus de la salida del paciente. 
              tr
                td.text-start 
                  p PAMEC: Programa de Auditora      para el Mejoramiento de la Calidad con enfoque en seguridad del paciente. 
                  p Ejemplo 1:  Procesos institucionales, cdigo 4.2.4, estndar de calidad: la institucin cuenta con un programa de orientacin y formacin a las personas que usan los equipos para garantizar un funcionamiento seguro, consistente y preciso. 
              tr
                th Inspeccin, vigilancia y control sanitario
                td.text-start
                  p Estndares de calidad de inspeccin, vigilancia y control:
                  p Ejemplo 1 - Acta general, condiciones sanitarias, estndar de calidad: El establecimiento mantiene las condiciones de en orden, aseo y cuenta con un programa de limpieza y desinfeccin para las instalaciones y equipos documentado e implementado.
                  p Ejemplo 2 - Acta de inspeccin, vigilancia y control sanitario establecimientos veterinarios y afines, condiciones especficas, estndar de calidad: Dispone de rea especfica para desarrollar procedimientos invasivos o intervenciones quirrgicas independiente y que cuente con superficies de fcil limpieza y desinfeccin. 
              tr
                th Estndares de calidad para certificacin institucional
                td.text-start 
                  p Normas ISO - Norma tcnica colombiana. 
                  p Ejemplo 1: NTC-ISO 9001 cuarta edicin. Numeral 8.2, requisitos para los productos y servicios, 8.2.1 comunicacin con el cliente, estndar de calidad: la comunicacin con los clientes debe incluir tratar las consultas, los contratos o los pedidos, incluyendo los cambios. 
                  p Ejemplo 2: ISO 9001:2008, numeral 6.3. Infraestructura, estndar de calidad:  la organizacin debe determinar, proporcionar y mantener la infraestructura necesaria para lograr la conformidad con los requisitos del producto. 

    p.mb-3(data-aos="fade-down") Teniendo en cuenta los elementos generales de los estndares de calidad, se procede a realizar la revisin de las metodologas que existen para el diseo de planes, programas y proyectos, as como aquellas que existen para el establecimiento de prioridades. 
    p La planeacin es la generacin de una idea que lleva a la formulacin de objetivos.  Son parte del planeamiento:
    ul.lista-ul.ms-3.mb-4
      li.mb-0
        span.icon  
        | Acciones estratgicas: representa los planteamientos y direccionamiento. 
      li.mb-0
        span.icon  
        | Acciones tcticas: operativa, realizacin de lo pensado o planteado en la accin estratgica.
    p.mb-5(data-aos="fade-down") El concepto de planeamiento, es el paso inicial para entrar en materia de las metodologas, en el siguiente recurso encontrar una mayor explicacin al respecto:

    .row.mb-5(data-aos="fade-down")
      .col-12.col-md-6
        img(src='@/assets/curso/temas/tema1/tema1-ac.png', alt='')
      .col-12.col-md-6
        LineaTiempoD.color-secundario
          div(numero="1" titulo="Planeamiento") 
            p El planeamiento ofrece ventajas al ayudar a identificar objetivos claros, determinar la estructura, establecer metas y planificar polticas, programas, procedimientos, reglas y estrategias. Incluye coordinacin de los colaboradores internos de la organizacin, recursos disponibles e identificar procedimientos eficientes para el logro de los objetivos.
          div(numero="2" titulo="Herramientas") 
            p Las herramientas del planeamiento, incluye los elementos con que se cuenta para poner en prctica lo calculado.
          div(numero="3" titulo="Proyecciones") 
            p Corresponde con los pronsticos basados en los datos estadsticos pasados.
          div(numero="4" titulo="Pronsticos") 
            p Son las predicciones del comportamiento a futuro basndose en aspectos subjetivos.
          div(numero="5" titulo="Planes") 
            p Son todas las acciones planeadas a futuro.
          div(numero="6" titulo="Programas") 
            p Forma de llevar a cabo los planes.
          div(numero="7" titulo="Proyectos") 
            p Procedimiento que se lleva a cabo.
          div(numero="8" titulo="Presupuestos") 
            p Representa todo el plan integral de una manera cuantitativa.
          div(numero="4" titulo="Procedimientos") 
            p Permiten organizar el paso a paso de un proceso.
    .row.justify-content-center.align-items-center.mb-5(data-aos="fade-down")
      .col-12.col-md-8.col-lg-8
        p En este componente formativo, slo se van a revisar las metodologas establecidas para los planes, programas y proyectos especficos. 
        p #[b Proyectos.] El proyecto, es el resultado del anlisis de todas las opciones estratgicas y se debe dar prioridad a cada una de stas, seleccionando aquel que tiene un desempeo nico como condicin para lograr los objetivos estratgicos, y de igual manera la misin y la visin empresarial. 
        p En los proyectos estratgicos, se puede tener en cuenta la modernizacin tecnolgica, la reingeniera organizacional, el plan global de mercado, de calidad total, de servicio al cliente y plan estratgico de talento humano. 
        p Para la seleccin de los proyectos es indispensable tener en cuenta los siguientes elementos: 
        ul.lista-ul.ms-3
          li.mb-0
            span.icon  
            | Visin y misin corporativa. 
          li.mb-0
            span.icon  
            | Los objetivos corporativos. 
      .col-10.col-md-4.col-lg-4
        img(src='@/assets/curso/temas/tema1/tema1-28.png', alt='')  

    p.mb-3(data-aos="fade-down") #[b Metodologas para el diseo de un proyecto.] El diseo del proyecto es uno de los momentos iniciales en donde se pueden plantear las ideas, mtodos, recursos y documentos necesarios para dejar como entregables. El diseo debe incorporar la estructuracin de objetivos y la utilizacin de medios visuales para notificar mejor esos objetivos. 
    p.mb-5(data-aos="fade-down") A nivel general, el diseo de un proyecto cuenta con los pasos que van desde la proyeccin de las metas y objetivos claves hasta la puesta en marcha y consolidacin de la meta definida. 

    SlyderF.mb-5(columnas="col-lg-6 col-xl-4" data-aos="fade-down-right")
      .tarjeta.color-primario.p-4.col-ipad
        .row.justify-content-center.mb-3
          .col-8
            img(src='@/assets/curso/temas/tema1/carrucel-1.png' alt='')
        h4.text-center Fase 1
        p Estructuracin de los objetivos del proyecto. Tener en cuenta formularlos basados en estos elementos: especficos, alcanzables, realistas y de duracin limitada. Se debe dejar organizado el cronograma y los entregables del proyecto.
      .tarjeta.color-primario.p-4.col-ipad
        .row.justify-content-center.mb-3
          .col-8
            img(src='@/assets/curso/temas/tema1/carrucel-2.png' alt='')
        h4.text-center Fase 2
        p.text-start Se deben establecer los resultados del proyecto. Estos resultados describen en mayor detalle las tareas especficas que se van alcanzar con el proyecto.
      .tarjeta.color-primario.p-4.col-ipad
        .row.justify-content-center.mb-3
          .col-8
            img(src='@/assets/curso/temas/tema1/carrucel-3.png' alt='')
        h4.text-center Fase 3
        p.text-start En esta fase se suelen conocer los principales riesgos y limitaciones. Es importante utilizar un registro de riesgos que permita analizar, documentar y poder resolver los diversos riesgos que surjan alrededor del proyecto. 
      .tarjeta.color-primario.p-4.col-ipad
        .row.justify-content-center.mb-3
          .col-8
            img(src='@/assets/curso/temas/tema1/carrucel-4.png' alt='')
        h4.text-center Fase 4
        p.text-start Utilizar medios visuales para definir mejor la estrategia del proyecto. Este tipo de estrategias permiten comunicar a los miembros del equipo el fin del proyecto. 
      .tarjeta.color-primario.p-4.col-ipad
        .row.justify-content-center.mb-3
          .col-8
            img(src='@/assets/curso/temas/tema1/carrucel-5.png' alt='')
        h4.text-center Fase 5
        p.text-start Proyeccin del presupuesto. Algunas veces, el presupuesto lo asigna la direccin/gerencia, debe alinearse con lo formulado. Hasta la aprobacin del presupuesto, se asignan: garantes del proyecto, escritura de documentos y formulacin de tareas. 
      .tarjeta.color-primario.p-4.col-ipad
        .row.justify-content-center.mb-3
          .col-8
            img(src='@/assets/curso/temas/tema1/carrucel-6.png' alt='')
        h4.text-center Fase 6
        p.text-start Organizar un plan de contingencia el cual respalda lo proyectado. Esta fase de plan de contingencia permitir la resolucin de inconvenientes que surjan con optimizacin de la eficiencia. 
      .tarjeta.color-primario.p-4.col-ipad
        .row.justify-content-center.mb-3
          .col-8
            img(src='@/assets/curso/temas/tema1/carrucel-7.png' alt='')
        h4.text-center Fase 7
        p.text-start Se realiza la documentacin de acontecimientos, que permite la realizacin de actividades a tiempo, detectando las desviaciones presentadas durante la ejecucin. La documentacin se debe realizar de principio hasta el fin del proyecto. 

    .row.justify-content-center.align-items-center.mb-5(data-aos="fade-down")
      .col-12.col-md-8.col-lg-8
        p Las herramientas ms populares en el diseo de un proyecto son: 
        ul.lista-ul.ms-3
          li.mb-0
            span.icon  
            | El diagrama de flujo: por medio de este se puede visualizar el paso a paso del proyecto. 
          li.mb-0
            span.icon  
            | El diagrama de #[em Gantt]: por medio de este grfico de barras horizontales se logra visualizar el cronograma del proyecto. Las barras muestran las tareas del proyecto y la longitud de las barras definen la cantidad de tiempo que tardarn en realizarse a completitud. 
          li.mb-0
            span.icon  
            | La estructura de desglose del trabajo (WBS): por medio de esta estrategia se desglosan todas las tareas del proyecto, permitiendo proyectar los entregables y siempre teniendo en cuenta los objetivos del proyecto.
          li.mb-0
            span.icon  
            | Mapas mentales: por medio del mapa mental se puede visualizar el proyecto y las tareas. 
          li.mb-0
            span.icon  
            | Diagrama de PERT: por medio de este diagrama se puede realizar proyeccin, organizacin y diseo de tareas. Buen uso de esta herramienta en proyectos complejos y en aquellos que es indispensable la proyeccin del tiempo.
  
      .col-10.col-md-4.col-lg-4
        img(src='@/assets/curso/temas/tema1/tema1-29.png', alt='')  

    p.mb-5(data-aos="fade-down") La siete (7) fases presentadas, corresponden de forma general con las de mayor uso para la formulacin de proyectos, sin embargo, es importante describir otras metodologas que, aunque no son de uso comn, pueden usarse de acuerdo con los objetivos o propsitos trazados, estas son: 

    .row.justify-content-center.align-items-center.mb-5(data-aos="fade-down")
      .col-12
        TabsA.color-acento-botones(data-aos="fade-down")
          .tarjeta.color-primario.p-4(titulo="Metodologa <em>Agile</em>")
            h4 Metodologa <em>Agile</em> 
            p Usa ciclos de desarrollo con lanzamientos cortos y usuales, denominados #[em sprints]; su objetivo es obtener informacin de los beneficiarios acerca de las necesidades encontradas. Esta retroalimentacin, permite contar con una estrategia de mejora continua durante todo el proceso. Esta metodologa se caracteriza por ser creativa, dinmica y flexible. Permite la interaccin con los individuos, presencia de #[em software] de trabajo, trabajo articulado con el cliente y respuestas ante los cambios. 
            p Para su desarrollo requiere: 
            ul.lista-ul.ms-3
              li.mb-0
                span.icon  
                | #[b Inicio:] objetivos del #[em sprint].
              li.mb-0
                span.icon  
                | #[b Desarrollo:] seguimiento diario.
              li.mb-0
                span.icon  
                | #[b Cierre:] revisin de cumplimiento. 
            p Esta metodologa permite la adaptacin y el ajuste de los objetivos planteados en el proyecto, genera la posibilidad de tener resultados que cumplan con las expectativas. Presenta otros tipos de estrategias acorde con las necesidades que se buscan suplir:  
            ul.lista-ul.ms-3
              li.mb-0
                span.icon  
                | #[em Scrum.]
              li.mb-0
                span.icon  
                | #[em Kanban.]
              li.mb-0
                span.icon  
                | #[em XP-Extrem Manufacturing.]
              li.mb-0
                span.icon  
                | #[em Lean.]
          .tarjeta.color-primario.p-4(titulo="Metodologa Cascada - <em>Waterfal</em>")
            h4 Metodologa Cascada - <em>Waterfal</em> 
            p Se realiza por medio de la divisin del proyecto en procesos a realizar de manera secuencial hasta obtener la meta definida. Los procesos en los que se divide son: inicio, planeacin, realizacin, seguimiento y cierre. Permite controlar todos los elementos generados en cada fase. 
            ul.lista-ul.ms-3
              li.mb-0
                span.icon  
                | #[b Inicio:] estructuracin del proyecto y consentimiento.
              li.mb-0
                span.icon  
                | #[b Planificacin:] proyectar los diversos planes de gestin. 
              li.mb-0
                span.icon  
                | #[b Ejecucin:] puesta en marcha de tareas planeadas para dar cumplimiento a los objetivos planeados. 
              li.mb-0
                span.icon  
                | #[b Monitorizacin y control:] se vigilan las tareas realizadas y se realiza comparacin con la proyeccin inicial. Acorde con los resultados, se aplican los correctivos necesarios.  
              li.mb-0
                span.icon  
                | #[b Cierre:] fin del proyecto teniendo en cuenta tres posibles resultados: cumplimiento de todas las tareas proyectadas, cumplimiento parcial de algunas tareas proyectadas y el incumplimiento de lo planeado, lo que convierte el proyecto en inviable o de difcil ejecucin.

          .tarjeta.color-primario.p-4(titulo="Metodologa de Cadena Crtica")
            h4 Metodologa de Cadena Crtica 
            p Est basada en la identificacin constante de todas las limitaciones que se presentan en la ejecucin del proyecto.
            p Se basa en los siguientes principios: 
            ul.lista-ul.ms-3
              li.mb-0
                span.icon  
                | El reconocimiento de las restricciones que pueden afectar al proyecto.
              li.mb-0
                span.icon  
                | La priorizacin de las tareas dentro de la cadena crtica. 
              li.mb-0
                span.icon  
                | Dejar de lado el resto de las tareas que no estn priorizadas en la cadena crtica.                      
          .tarjeta.color-primario.p-4(titulo="Metodologa Hbrida")
            h4 Metodologa Hbrida 
            p Requiere que los tiempos de ejecucin sean cortos y tambin flexibles. Este mtodo consiste bsicamente en realizar mejora continua de los procesos, acomodarse a los cambios que se generen y crear todos los escenarios que tengan dinamismo y colaboracin. Se realiza por medio de la metodologa en cascada y la metodologa gil, por tanto, se prioriza tanto el cliente como el producto. 

    p.mb-4(data-aos="fade-down") En relacin a la metodologa #[em Agile], se usan diferentes #[em software] para su implementacin, que se explican brevemente:

    .row.justify-content-center.align-items-center.mb-5(data-aos="fade-down")
      .col-12
        AcordionA(tipo="a" clase-tarjeta="tarjeta color-primario-c" data-aos="fade-left")
          .row(titulo="<em>Scrum</em>")
            .col-6.mb-4.mb-md-0
              p Se generan #[em sprints] en perodos de 1 mes. En este tiempo se proyectan objetivos, se trabaja su ejecucin y se hace seguimiento en el marco de las necesidades de los clientes.  
              p El mtodo #[em Scrum] se trabaja en equipo, con proyectos complejos, por tener alto flujo de cambios a ltimo momento. Se basa en: transparencia, revisin continua y ajuste.  
            .col-md-6
              img(src='@/assets/curso/temas/tema1/tema1-33.png', alt='') 
          .row(titulo="<em>Kanban</em>")
            .col-6.mb-4.mb-md-0
              p Se enfoca en las actividades ms relevantes y utiliza las denominadas tarjetas de tipo visual (son adhesivas y se ponen sobre un tablero o pizarra). El objetivo es priorizar las tareas pendientes de realizar, para que el trabajo global fluya. El mtodo recibe soporte mediante plataformas como #[em Trello] y la combinacin con otros mtodos con el diagrama de #[em Gantt]. 
            .col-md-6
              img(src='@/assets/curso/temas/tema1/tema1-33.png', alt='') 
          .row(titulo="<em>XP-Extreme Manufacturing</em>")
            .col-6.mb-4.mb-md-0
              p Este mtodo se basa en potencializar las relaciones de tipo interpersonal asegurando xito en el proyecto. Divide el proyecto en fases y, realizando en cada una un ciclo completo de anlisis, diseo, desarrollo y pruebas. Idneo para proyectos con requisitos imprecisos y muy cambiantes, al poner el nfasis en la retroalimentacin continua entre el cliente y el equipo de desarrollo. 
            .col-md-6
              img(src='@/assets/curso/temas/tema1/tema1-33.png', alt='') 
          .row(titulo="<em>Lean</em>")
            .col-6.mb-4.mb-md-0
              p En este mtodo se tiene como objetivo darle mayor valor al cliente y disminuir la posibilidad de que se presente desperdicio de los recursos, respondiendo a los cambios del mercado y a la satisfaccin de las demandas del consumidor, todo esto enmarcado en la mnima utilizacin de recursos posibles y eliminando los desperdicios que no aportan valor.
            .col-md-6
              img(src='@/assets/curso/temas/tema1/tema1-33.png', alt='') 

    .row.justify-content-center.align-items-center.mb-5(data-aos="fade-down")
      .col-12.col-md-8.col-lg-8
        p.mb-4(data-aos="fade-down") Como se indic anteriormente, la metodologa que atae a este componente formativo, solamente revisar planes, programas y proyectos especficos, en relacin a los programas, se encuentran la siguiente descripcin:
        ul.lista-ul.ms-3
          li.mb-0
            span.icon  
            | #[b Programas.] Los programas sirven para darle ejecucin a los planes y contienen recursos para la obtencin de los resultados esperados. Un programa es considerado como un proceso que debe ser sistemtico, coherente y lgico. Acorde con el programa de formacin Gestin de la Salud Ambiental y Seguridad Sanitaria, los tipos de programas que se pueden proyectar buscan mejorar las condiciones de salud por medio del impacto positivo de la problemtica de salud existente. 
        p.mb-4(data-aos="fade-down") Dadas las proyecciones de este tipo de programas, es necesario que los programas se retroalimenten de manera permanente en funcin de los resultados y el logro de los objetivos. Para la planeacin y desarrollo de los programas se cuenta con la siguiente metodologa:
      .col-10.col-md-4.col-lg-4
        img(src='@/assets/curso/temas/tema1/tema1-34.png', alt='')  

    TabsC.color-acento-botones.mb-5(data-aos="zoom-in-down")
      .py-3.py-md-4(titulo="Diagnstico")
        .row
          .col-md-5.mb-4.mb-md-0
            figure
              img(src='@/assets/curso/temas/tema1/tema1-35.png', alt='')
          .col-md-7
            p Se debe definir la situacin actual de la poblacin objeto, -por ejemplo-, que se desea impactar. En el marco de los programas orientados a impactar las condiciones de salud en el mbito de salud ambiental de las personas, se debe tener en cuenta la caracterizacin de tipo demogrfico, poblacional y territorial. Esta caracterizacin tiene en cuenta el ambiente socioeconmico, el entorno fsico, los hbitos personales de salud y las prioridades de salud. En este diagnstico es importante realizar un anlisis de la oferta y demanda de los servicios de salud y tambin de la organizacin en la prestacin pblica de los servicios de salud.
      .py-3.py-md-4(titulo="Planificacin")
        .row
          .col-md-5.mb-4.mb-md-0
            figure
              img(src='@/assets/curso/temas/tema1/tema1-36.png', alt='')
          .col-md-7
            p Es necesario proyectar los requerimientos en trminos de recursos: humanos, econmicos, logsticos; y realizar su distribucin acorde con las acciones a desarrollar, el tiempo estimado y el nmero de personas que se desea impactar.
            p Durante la planificacin tambin se deben generar las siguientes actividades: 
            ul.lista-ul.ms-3
              li.mb-0
                span.icon  
                | Introduccin, antecedentes, justificacin, y objetivos del programa. 
              li.mb-0
                span.icon  
                | Definir metas e indicadores. 
              li.mb-0
                span.icon  
                | Actividades y procedimientos. 
              li.mb-0
                span.icon  
                | Proyectar los entregables de la ejecucin del programa o describir el resultado esperado. 
              li.mb-0
                span.icon  
                | Participantes; es necesario dejar claro el universo del programa; poblacin a la que est dirigido. As como la organizacin interna del programa; indicar los colaboradores internos del programa. 
              li.mb-0
                span.icon  
                | Cronograma (fecha inicial/final); si es continuo, el cronograma estar enfocado en la ejecucin de las actividades incluidas en el desarrollo. 
              li.mb-0
                span.icon  
                | Conocer posibles riesgos/restricciones que sean relevantes.
      .py-3.py-md-4(titulo="Ejecucin")
        .row
          .col-md-5.mb-4.mb-md-0
            figure
              img(src='@/assets/curso/temas/tema1/tema1-37.png', alt='')
          .col-md-7
            p Se operativiza el programa teniendo en cuenta todos los elementos definidos en la planificacin.
      .py-3.py-md-4(titulo="Evaluacin")
        .row
          .col-md-5.mb-4.mb-md-0
            figure
              img(src='@/assets/curso/temas/tema1/tema1-38.png', alt='')
          .col-md-7
            //- h4 Evaluacin
            p Esta fase valora los resultados obtenidos mediante la implementacin del programa teniendo en cuenta aspectos susceptibles de mejorar. Se puede concretar otros aspectos relevantes en torno a lo definido inicialmente, lo que permite tener control del programa.
            p Debe contener aspectos claves como:
            .row 
              .col-12.col-md-6
                ul.lista-ul.ms-3
                  li.mb-0
                    span.icon  
                    | Introduccin 
                  li.mb-0
                    span.icon  
                    | Antecedentes 
                  li.mb-0
                    span.icon  
                    | Justificacin
                  li.mb-0
                    span.icon  
                    | Objetivos 
                  li.mb-0
                    span.icon  
                    | Metas 
                  li.mb-0
                    span.icon  
                    | Universo 
                  li.mb-0
                    span.icon  
                    | Organizacin 
              .col-12.col-md-6
                ul.lista-ul.ms-3
                  li.mb-0
                    span.icon  
                    | Recursos 
                  li.mb-0
                    span.icon  
                    | Actividades 
                  li.mb-0
                    span.icon  
                    | Informacin 
                  li.mb-0
                    span.icon  
                    | Supervisin 
                  li.mb-0
                    span.icon  
                    | Evaluacin 
                  li.mb-0
                    span.icon  
                    | Cronograma 
                  li.mb-0
                    span.icon  
                    | Bibliografa
 
    p.mb-5(data-aos="fade-down") Una explicacin ms detallada respecto de los elementos claves que debe contener un programa puede encontrarla en el siguiente cuadro:

    .row.justify-content-center.align-items-center.mb-5
      .col-12.col-lg-12(data-aos="fade-right")
        .titulo-sexto.color-acento-contenido.mb-1
          p.titulo.pb-0.mb-0 #[b Tabla 3.] #[i Elementos claves que debe contener un programa] 
        .tabla-a.color-acento-contenido
          table(style="min-width:768px")
            thead
              tr.cabecera-a.text-center
                td(style="width:25%") Elemento
                td Descripcin
            tbody
              tr
                th Introduccin
                td.text-start Resume el contenido general incluyendo propsitos, objetivos y procedimientos a tener en cuenta. 
              tr
                th Antecedentes
                td.text-start Son todos los elementos que dieron origen al programa teniendo en cuenta aspectos demogrficos, histricos, socioculturales y socioeconmicos. 
              tr
                th Justificacin
                td.text-start Describe el por qu, y el para, mediante la definicin del problema, la trascendencia, la factibilidad y la vulnerabilidad. 
              tr
                th Objetivos
                td.text-start Define los logros que se desean obtener con la ejecucin del programa. Estos objetivos deben ser claros y de fcil medicin. 
              tr
                th Metas
                td.text-start Permiten tener la traduccin cuantitativa de los objetivos. Se deben definir en trminos porcentuales o nmeros absolutos. 
              tr
                th Universo
                td.text-start Describe la poblacin objeto a la que est dirigida la propuesta del programa. 
              tr
                th Organizacin
                td.text-start Enuncian los niveles jerrquicos de la institucin que participan en la planeacin, realizacin y control del programa.
              tr
                th Recursos
                td.text-start Describe los recursos humanos (perfil y experiencia ocupacional), materiales (detallar nmero, tipo y costos de los insumos, equipos y dems elementos) y financieros (valor total para operativizar el programa). 
              tr
                th Actividades
                td.text-start Describe las actividades y procedimientos que se ejecutan acorde con los recursos que participan. 
              tr
                th Informacin
                td.text-start Contempla todos los aspectos relacionados que se requiere conocer respecto a la problemtica, actores y espacios involucrados.
              tr
                th Supervisin
                td.text-start Identifica las posibles desviaciones con el fin de implementar las acciones correctivas que se deban realizar. Esta supervisin est enfocada en dar apoyo y asesora al personal que hace parte de la ejecucin del programa. 
              tr
                th Evaluacin
                td.text-start Permite definir el alcance de los objetivos, metas y por tanto el impacto del programa. 
              tr
                th Cronograma
                td.text-start Consiste en los tiempos determinados para dar cumplimiento al programa. Puede tener en cuenta el diagrama de #[em Gantt]. 
              tr
                th Bibliografa
                td.text-start Referencia todos aquellos referentes de consulta, que se tuvieron en cuenta para la formulacin del programa. 

    .row.justify-content-center.align-items-center.mb-5(data-aos="fade-down")
      .col-12
        p.mb-4(data-aos="fade-down") Y en relacin a los planes, es importante tener presente, que la formulacin y puesta en marcha a este respecto, es de suma importancia para la consecucin de metas y objetivos.
        ul.lista-ul.ms-3
          li.mb-0
            span.icon  
            | #[b Planes.] Los planes son aquellos que permiten organizar un proceso paso a paso, son usados en la planificacin estratgica, la cual programa los objetivos a largo plazo. Por medio del plan de accin se le permite a la organizacin describir con exactitud cmo se van a lograr los objetivos estratgicos definidos. 
        p.mb-4(data-aos="fade-down") Los pasos esenciales para el diseo de un plan de accin son:

    SlyderF.mb-5(columnas="col-lg-6 col-xl-4" data-aos="fade-down-right")
      .tarjeta.color-primario.p-4.col-ipad
        .row.justify-content-center.mb-3
          .col-8
            img(src='@/assets/curso/temas/tema1/carrucel-2a.png' alt='')
        p Se deben crear de manera correcta los objetivos, tanto los de largo, mediano y corto plazo. 
      .tarjeta.color-primario.p-4.col-ipad
        .row.justify-content-center.mb-3
          .col-8
            img(src='@/assets/curso/temas/tema1/carrucel-2b.png' alt='')
        p Definir las estrategias que se requieren para alcanzar las metas. 
      .tarjeta.color-primario.p-4.col-ipad
        .row.justify-content-center.mb-3
          .col-8
            img(src='@/assets/curso/temas/tema1/carrucel-2c.png' alt='')
        p Concretar las tareas y jerarquizarlas acorde con el nivel de urgencia, importancia y las que se puedan abordar como elementos menos urgentes. 
      .tarjeta.color-primario.p-4.col-ipad
        .row.justify-content-center.mb-3
          .col-8
            img(src='@/assets/curso/temas/tema1/carrucel-2d.png' alt='')
        p Exponer los tiempos de inicio y de fin de cada actividad a realizar. 
      .tarjeta.color-primario.p-4.col-ipad
        .row.justify-content-center.mb-3
          .col-8
            img(src='@/assets/curso/temas/tema1/carrucel-2e.png' alt='')
        p Asignar las personas lderes y responsables de las actividades proyectadas.
