      .col-12.col-md-8.col-lg-8
        p.mb-4(data-aos="fade-down") Acorde con lo expuesto anteriormente, y adems de conocer las metodologas para el diseo de los planes, programas y proyectos, contar con aquellos mtodos que sirven como apoyo en la identificacin de prioridades que deben ser abordadas. #[b La metodologa de priorizacin] debe estar orientada hacia los procesos que impactan con mayor fuerza al usuario y a los procesos de calidad de la institucin. Para realizar esta priorizacin se cuenta con varias tcnicas, aunque se destacan las siguientes: la matriz de priorizacin, y el diagrama de Pareto. La eleccin de cualquiera de estas, requiere de la evaluacin de criterios esenciales los cuales son: 
