        p.mb-0 #[b Metodologa Matriz de priorizacin:] este tipo de metodologa se caracteriza por presentar un grupo de criterios que cuentan con un ponderado versus el grupo de opciones que conllevan a una calificacin por medio de matemtica esencial. Para realizar una matriz de priorizacin se debe tener en cuenta elementos claves como: conocer el objetivo de utilizar la matriz, definir o revisar las opciones con las cuales se va a construir la matriz, formular los criterios, asignar la respectiva ponderacin a cada criterio, calificar cada opcin acorde con el criterio determinado y enfrentar todas las opciones entre s, acorde con el nmero total criterios existentes. 

