          p Metodologa Diagrama de Pareto: por medio de esta herramienta se puede asignar la prioridad a los problemas de calidad, por medio del diagnstico de las causas y la solucin de las mismas. El diagrama de Pareto es un grfico que permite clasificar de izquierda a derecha en un orden de tipo descendente las causas o factores detectados en funcin de un fenmeno especfico.  Acorde con el principio o regla de Pareto, se sugiere que el 80 % de las consecuencias proviene del 20 % de las causas; este fue el fundamento emprico del creador del Diagrama, el seor Vilfredo Pareto. Por medio del diagrama se puede conocer los problemas que tengan una representacin del 80 %. 

    p.mb-3(data-aos="fade-down") Los pasos para realizar el diagrama de Pareto son: 

    ul.lista-ul.ms-3(data-aos="fade-down")
      li.mb-0
        span.icon  
        | Definir la situacin problema.
      li.mb-0
        span.icon  
        | Enunciar las causas o categoras en funcin de la situacin problemtica. Se debe incluir el tiempo.
      li.mb-0
        span.icon  
        | Realizar la recoleccin de datos.
      li.mb-0
        span.icon  
        | Organizar las causas de mayor a menor teniendo en cuenta los datos recolectados y las medidas implicadas en estos datos.
      li.mb-0
        span.icon  
        | Calcular los datos recolectados, teniendo en cuenta el acumulado, el porcentaje y el porcentaje acumulado.
      li.mb-0
        span.icon  
        | Se debe generar la grfica de las causas. En el eje X se ponen las causas y en el eje Y izquierdo (frecuencia de la causa) y el Y derecho (porcentaje acumulado de 0 a 100 %); el eje Y derecho es el que sirve para graficar la curva acumulada. 
      li.mb-0
        span.icon  
        | Se debe analizar el diagrama.

