      h2 1.2 Actividades
    
    p.mb-5(data-aos="fade-down") Los procedimientos son definidos como un grupo de actividades relacionadas entre s, las cuales se muestran de forma cronolgica y ayudan a precisar el quin, qu, cmo, cundo, dnde y con qu se debe desarrollar cada actividad. Por lo general, el procedimiento debe estar conformado por el objetivo, las normas de ejecucin, los formatos a utilizar y dems documentos que se incluyan dentro de las actividades como necesarios para la ejecucin de lo requerido. 

    .row.justify-content-center.mb-5(data-aos="fade-down")
      .col-12.col-lg-10
        .cajon.color-primario.p-4.px-lg-5
          p.mb-0 La actividad es, por tanto, el grupo de acciones alineadas entre s y de ejecucin secuencial realizadas por un grupo responsable y previamente asignado.

    p.mb-3(data-aos="fade-down") Se recomienda que la redaccin de actividades se realice de la siguiente manera: 
    
    ul.lista-ul.ms-3.mb-5(data-aos="fade-down")
      li.mb-0
        span.icon  
        | Utilizar verbos sustantivados: admisin, almacenamiento, acompaamiento, por ejemplo.
      li.mb-0
        span.icon  
        | Dar respuesta al cmo, de qu, con qu, quin y para qu.

    p.mb-3(data-aos="fade-down") Es necesario aclarar la diferencia entre actividad y tarea; la tarea corresponde con una unidad especfica en el orden de trabajo, se crea como parte de una actividad; la actividad es la descripcin del tipo de orden de trabajo, para una mejor comprensin, se ofrece el siguiente ejemplo: 
    p.mb-5(data-aos="fade-down") Las actividades se establecen mediante los tiempos asumidos para su desarrollo, y pueden ser:


    .row.mb-5(data-aos="zoom-in-down")
      .col-sm-6.col-lg-4.mb-4.mb-lg-0.d-flex.align-items-stretch
