          p #[b Actividades a mediano plazo:] corresponde con aquellas actividades con margen de cumplimiento anual o bianual. Por lo general las actividades a mediano plazo se dan como resultado de las de corto plazo. 
      .col-sm-6.col-lg-4.mb-4.mb-sm-0.d-flex.align-items-stretch
        .tarjeta--boton.color-primario.p-4.w-100
          .row.justify-content-center.mb-3
            .col-7.col-lg-6
              figure
                img(src='@/assets/curso/temas/tema1/tema1-49.svg', alt='')
          p #[b Actividades a largo plazo:] corresponde con las metas ms lejanas en tiempo y dependen del cumplimiento de las actividades de corto y mediano plazo. Por lo general se establecen para un perodo de 5 a 10 aos. 

    .row.justify-content-center.align-items-center.mb-5(data-aos="fade-down")
      .col-12.col-md-8.col-lg-8
        p Recordar que para el cumplimiento efecto de las actividades se cuenta con el cronograma de trabajo, el cual se puede representar en el Diagrama de #[em Gantt] para su fcil ejecucin y seguimiento. 
        p.mb-3 La estandarizacin de procedimientos es necesaria para la planeacin general y la gestin de todas las actividades que impactan la calidad en los procesos dentro de la organizacin. Cada procedimiento documentado y estandarizado puede cubrir uno o varios elementos del sistema de calidad general los cuales estn interrelacionados entre s.  Por medio de la estandarizacin de procedimientos, la cual se lleva a cabo en las mismas estructuras y formatos, se pueden obtener los siguientes beneficios: 
        ul.lista-ul.ms-3
          li.mb-0
            span.icon  
            | Favorecer la familiaridad del cliente interno y externo con las actividades a realizar y los formatos que se requieren para llevarla a cabo.
